/* End CSS Reset */

:root {
    --red: #FC4747;
    --black: #10141E;
    --light-blue-grey: #5A698F;
    --dark-blue-grey: #161D2F;
    --white: #FFFFFF;
}

html {
    overflow-x: hidden;
}

html * {
    font-family: 'Outfit', sans-serif !important;

}

body {
    background-color: var(--black);
    color: white;
    overflow-x: hidden;
}

main {
    width: 100vw;
    overflow: hidden;
}

h1, h2, p, .text-light {
    font-weight: 300;
}

h3, h4 {
    font-weight: 500;
}

h1 {
    font-size: 2rem;
}

h2, h3 {
    font-size: 1.5rem;
}

h4 {
    font-size: 1.125rem;
}

p, .button-red, .button-white, .text-light-large {
    font-size: 0.938rem;
}

.text-light-large, .text-light-small {
    opacity: 0.75;
    mix-blend-mode: normal;
}

.text-light-small {
    font-size: 0.813rem;
}

.red-text {
    color: var(--red);
}

.dark-text {
    color: var(--dark-blue-grey)
}

.button-red, .button-white {
    cursor: pointer;
    padding: 15px 95px 15px 95px;
    border: none;
    border-radius: 5px;
    font-weight: 300;
}

.button-red {
    background-color: var(--red);
    color: var(--white);
}

.button-white {
    background-color: var(--white);
    color: var(--dark-blue-grey);
}

.header {
    background-color: var(--dark-blue-grey);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px 15px 30px;
}

.header>*, .bookmark-icon, .play-icon, .hover-video-player {
    cursor: pointer;
}

.header-icons {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
}

.header-icons>* {
    margin: 0px 10px 0px 10px;
    width: 20px;
    height: 20px;
}

.avatar {
    max-width: 40px;
    height: auto;
    border: 1px solid var(--white);
    border-radius: 50%;
}

.search-bar {
    display: flex;
    align-items: center;
    padding: 30px 0px 30px 0px;
    margin: 0px 30px 0px 30px;
}

.search {
    background-color: var(--black);
    color: var(--white);
    outline: none;
    border: none;
    margin-left: 10px;
    width: calc(100% - 20px);
    caret-color: var(--red);
}

.bookmark-icon-container, .card-info-row, .img-tile, .img-tile-overlay {
    display: flex;
}

.bookmark-icon-container {
    background-color: var(--black);
    opacity: 0.5;
    border-radius: 50%;
    align-self: flex-end;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
}

.card {
    max-width: 400px;
}

.card-info-row {
    align-items: center;
    align-self: flex-start;
}

.card-info-row>p, .card-info-row>img {
    margin-right: 10px;
}

.movie-title {
    align-self: flex-start;
    margin: 0px;
}

.card>h2 {
    margin-top: 0px;
}

.img-tile-container {
    border-radius: 20px;
    background-repeat: no-repeat;
    background-size: cover;
}

.img-tile, .img-tile-overlay {
    height: 150px;
    border-radius: 20px;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 15px;

}

.img-tile-overlay {
    background-color: rgba(0, 0, 0, 0.6);
}

.results-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 30px;
    padding: 0px 30px 0px 30px;
    margin-bottom: 30px;

}

.hover-video-player {
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 30px;
    padding: 15px;
    margin: auto;
}

.hover-video-player>* {
    margin: 0px 5px 0px 5px;
}

.title {
    margin: 0px 0px 30px 30px;
}


.swiper-container-div {
    width: 100%;
    overflow: hidden;
    margin-bottom: 30px;
}

.swiper {
    width: 100%;
    height: 100%;
    margin-left: 30px !important;
    overflow-x: hidden;
}

.swiper-slide {
    background: var(--black);
}

@media only screen and (min-width: 1200px) {
    .app {
        display: flex;
    }

    .header {
        flex-direction: column;
        height: 100vh;
        position: fixed;
        justify-content: space-between;
    }

    .header-icons {
        flex-direction: column;
    }

    .header-icons>* {
        margin: 10px;
    }

    main {
        margin-left: 100px;
    }

    .logo {
        margin-top: 20px;
    }

    .avatar {
        margin-bottom: 50px;
    }


}


@media only screen and (max-width: 610px) {
    .card {
        max-width: none;
    }

    .swiper {
        margin-left: 0px;
        width: 90%;
    }

    .img-tile-container {
        width: calc(100vw - 60px);
    }
}